export function clickEvents(panel, sectionParts, evt) {
    panel.clickEvent(evt);
    for (var i = 0; i < sectionParts.length; i++) {
        var sectionPart = sectionParts[i];
        if (!sectionPart)
            continue;
        if (!sectionPart.clickEvent)
            continue;
        var match = sectionPart.clickEvent(evt);
        if (match)
            break;
    }
}
export function keyboardEvents(sectionParts, evt) {
    for (var i = 0; i < sectionParts.length; i++) {
        var sectionPart = sectionParts[i];
        if (!sectionPart)
            continue;
        if (!sectionPart.keyboardEvent)
            continue;
        var match = sectionPart.keyboardEvent(evt);
        if (match)
            break;
    }
}
