import alarm from "@assets/audio/alarm.mp3";
import Audio from "./Audio";
import AudioController from "./AudioController";
import "@styles/components/clock/DigitalAlarm.css";
var DigitalAlarm = /** @class */ (function () {
    function DigitalAlarm() {
        this.audio = new Audio({ src: alarm });
        this.controller = new AudioController({
            audio: this.audio,
            text: "Iniciar alarma"
        });
        this.self = this.template();
    }
    DigitalAlarm.prototype.clickEvent = function (evt) {
        var clickOnButton = evt.target.matches("." + AudioController.className);
        if (!clickOnButton)
            return false;
        if (this.audio.isRinging) {
            this.audio.pause();
            this.controller.changeText("Iniciar alarma");
        }
        else {
            this.audio.play();
            this.controller.changeText("Detener alarma");
        }
        return true;
    };
    DigitalAlarm.prototype.template = function () {
        var digitalAlarm = document.createElement("div");
        digitalAlarm.classList.add(DigitalAlarm.className);
        var alarm = document.createElement("figure");
        alarm.append(this.audio.self);
        digitalAlarm.append(alarm);
        digitalAlarm.append(this.controller.self);
        return digitalAlarm;
    };
    DigitalAlarm.className = "digital-alarm";
    return DigitalAlarm;
}());
export default DigitalAlarm;
